import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { ICreateRemito, IRemito } from '../../models/remito';
import { RemitosService } from '../../service/remitos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { BehaviorSubject, Observable, Subject, combineLatest, debounceTime, finalize, firstValueFrom, map, startWith, switchMap, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatTableDataSource } from '@angular/material/table';
import { DatePipe } from '@angular/common';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IAlertConfig } from '@shared/models/alert';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { IItemOrdenCompraPendiente } from 'app/modules/compras/ordenes-de-compra/models/orden-de-compra';

@Component({
    selector: 'app-save-remitos',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class SaveComponent implements OnInit {
    // Necesarios para la app
    title: string;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    maxPalabras: number = 150;
    minDate: Date;
    maxDate: Date;
    deposito_default = 0;

    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    // Particulares del modulo
    columns = ['codigointerno', 'articulo', 'unidadmedida', 'cantidad_pedida', 'cantidad_pendiente', 'cantidad_a_recibir'];
    filteredDepositos: Observable<IDeposito[]>;
    filteredProveedores: Observable<IProveedor[]>;
    filteredOrdenesDeCompraSubject = new BehaviorSubject<any[]>([]);
    form: FormGroup;
    remito: IRemito;
    user: any;
    items_oc_pendientes_aux: IItemOrdenCompraPendiente[] = [];
    items_oc_pendientes: any[] = [];
    proveedores: any[] = [];
    depositos: IDeposito[] = [];
    orden_compras: any[] = [];
    oc_pendientes = new BehaviorSubject<any[]>([]);;
    mov_art_list: any[] = [];

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _remitosService: RemitosService,
        private _empresaService: EmpresaService,
        private datePipe: DatePipe,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>,
    ) {
        this.remito = this.data?.soloLectura === true ? this.data?.model : null;
        this.title = this.data?.soloLectura === true ? this.data.title : `Nuevo Remito`;

        this.user = JSON.parse(localStorage.getItem('user'));

        // Se define minDate como null o cualquier otra fecha límite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
                this.deposito_default = empresa.deposito_default;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.loadServices();

        this.subscribeAlert();

        this.setupProveedorValueChange();
        this.setupOrdenCompraValueChange();
    }

    /**
    * Metodo de alertas
    */
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    /**
     * Este metodo inicializa el formulario
     */
    newForm() {
        const [dia, mes, anio] = this.remito ? this.remito.fecha.split('/') : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.


        this.form = new FormGroup({
            fecha: new FormControl({ disabled: this.data?.soloLectura ? true : false, value: (this.remito?.fecha ? this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')) }, [Validators.required]),
            hora: new FormControl({ disabled: this.data?.soloLectura ? true : false, value: (this.remito?.hora ? this.remito.hora : this.datePipe.transform(new Date(), 'HH:mm')) }, [Validators.required]),
            proveedor: new FormControl({ disabled: this.data?.soloLectura ? true : false, value: (this.remito ? this.remito.descripcion_proveedor : '') }, [Validators.required]),
            oc_pendiente: new FormControl({ disabled: this.data?.soloLectura ? true : false, value: (this.remito ? { idordencompra: this.remito.idordencompra, numero: this.remito.nro_orden_compra, fecha: this.remito.fecha_oc } : '') }, [Validators.required]),
            deposito: new FormControl({ disabled: this.data?.soloLectura ? true : false, value: (this.remito ? this.remito.descripcion_deposito : '') }, Validators.required),
            observaciones: new FormControl({ disabled: this.data?.soloLectura ? true : false, value: (this.remito ? this.remito.observaciones : '') }, [Validators.maxLength(this.maxPalabras)]),
        });
    }

    /**
     * Este metodo ejecuta todas las peticiones asincronas al mismo tiempo
     */
    async loadServices() {
        await Promise.all([
            this.getDepositos(),
            this.getOrdenCompras(),
            this.getItemsOrdenDeCompraPendiente(),
        ])
    }

    // ############### Llamados a los servicios ###################

    /**
     * Controlador que obtiene los depósitos
     * @param option 
     */
    async getDepositos(option?) {
        const Depositos = await firstValueFrom(this._remitosService.getDepositos(option));
        new Promise(async (resolve) => {
            this.depositos = Depositos.list;
            this.filteredDepositos = this.form.get('deposito').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterDepositos(value)),
            );

            // Filtrar y autoasignar el depósito por defecto
            const depositoPorDefecto = this.depositos.find(deposito => deposito.iddeposito === this.deposito_default);
            if (depositoPorDefecto && !this.remito) {
                this.form.get('deposito').setValue(depositoPorDefecto);
            }
            resolve(true);
        })
    };

    /**
     * Este metodo obtiene las ordenes de compra pendientes de entrega con sus items
     * @param object 
     */
    async getItemsOrdenDeCompraPendiente(object?) {

        if (this.remito) {
            object = {
                idremito: this.remito.idremito
            }
        }

        const OrdenesDeCompraPendiente = await firstValueFrom(this._remitosService.getItemsOcPendiente(object));

        //Encapsulo solo las propiedades que quiero utilizar.
        this.items_oc_pendientes_aux = OrdenesDeCompraPendiente.list.map((item: IItemOrdenCompraPendiente) => {
            return {
                iditemordencompra: item.iditemordencompra,
                idordencompra: item.idordencompra,
                idarticulo: item.idarticulo,
                codigo_interno_articulo: item.codigo_interno_articulo,
                descripcion_articulo: item.descripcion_articulo,
                idunidadmedida: item.idunidadmedida,
                descripcion_um: item.descripcion_um,
                cantidad_entregada: Number(item.cantidad_entregada),
                cantidad_pendiente: Number(item.cantidad_pendiente),
                cantidad_pedida: Number(item.cantidad_pedida),
            }
        });

        this.items_oc_pendientes = this.items_oc_pendientes_aux;

        if (this.remito) this.crearGrillaForm(this.items_oc_pendientes);
    }

    /**
     * Este metodo obtiene los movimientos realizados
     * @param idremito 
     * @returns 
     */
    async getItems(idremito): Promise<any> {
        const filter = {
            idremito: idremito,
            idordencompra: null
        }

        return new Promise(async (resolve) => {
            const items = await firstValueFrom(this._remitosService.getItemsOcPendiente(filter));
            this.mov_art_list = items.list;
            resolve(this.mov_art_list);
        })
    }

    /**
    * Esta funcion es la encargada de obtener los datos de las ordenes de compra y que no se repitan.
    * @param idproveedor Cuando queremos obtener solo las ordenes de compra de un proveedor utilicamos su id para filtrar.
    */
    async getOrdenCompras(event?) {
        let filter = {
            idproveedor: event?.Codigo ? event?.Codigo : null,
            idordencompra: event?.idordencompra ? event?.idordencompra : null
        }

        const ordenes_de_compra = await firstValueFrom(this._remitosService.getOrdenesDeCompra(filter));

        this.orden_compras = ordenes_de_compra.result.map(oc => { return { ...oc, idordencompra: oc.codigo } });

        // Ordenar las ordenes de compra por fecha (en formato 'yyyy-mm-dd')
        this.orden_compras.sort((a, b) => {
            const fechaA = new Date(a.fecha); // Convertimos la cadena de texto en un objeto Date
            const fechaB = new Date(b.fecha); // Convertimos la cadena de texto en un objeto Date
            return fechaB.getTime() - fechaA.getTime(); // Comparamos las marcas de tiempo
        });

        this.oc_pendientes.next(this.orden_compras);

        //Encapsulo solo las propiedades que quiero utilizar.
        this.proveedores = this.orden_compras.map(p => {
            return {
                Codigo: p.proveedor,
                RazonSocial: p.proveedor_descripcion
            }
        }).sort((a, b) => {
            return a.RazonSocial.localeCompare(b.RazonSocial); // Ordena alfabéticamente
        });

        this.proveedores = Array.from(new Map(this.proveedores.map(p => [p.Codigo, p])).values());

        new Promise(async (resolve) => {
            //Esta fraccion de codigo es para filtrar lo que se escriba en el input.
            this.filteredProveedores = this.form.get('proveedor').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterProveedores(value)),
            );
            resolve(true);
        });
    }

    /**
     * Metodo de persistencia de datos
     */
    onSubmit() {
        // Verificar la longitud de las observaciones
        if (!this.isPalabraMaxLengthReached()) {
            return this.alert.error(`La longitud máxima de observaciones es de ${this.maxPalabras} caracteres.`);
        }

        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            // Validar que se haya seleccionado un proveedor válido solo si se está agregando un nuevo remito o se cambió el proveedor
            if (!this.remito || (this.remito && this.form.get('proveedor').value !== this.remito.descripcion_proveedor)) {
                const proveedorSeleccionado = this.form.get('proveedor').value;
                const proveedorExistente = this.proveedores.find(proveedor => proveedor.Codigo === proveedorSeleccionado.Codigo);
                if (!proveedorExistente) {
                    return this.alert.error('El proveedor seleccionado no existe. Por favor, seleccione un proveedor válido.');
                }
            }

            const existe_cantidad_item_mayor_a_la_solicitada = this.mov_art_list.find((item: IItemOrdenCompraPendiente) => item.cantidad_a_recibir.value > item.cantidad_pedida['value']);
            if (existe_cantidad_item_mayor_a_la_solicitada) {
                return this.alert.error('Error al cargar Remito. No se puede recibir una cantidad mayor a la pedida');
            }

            let movimientos = this.mov_art_list.map((objeto: IItemOrdenCompraPendiente) => ({   //Encapsulo solamente los datos que utilizaré de los articulos...
                idordencompraitem: objeto.iditemordencompra,
                idarticulo: objeto.idarticulo,
                cantidad: objeto.cantidad_a_recibir.value,
                idunidadmedida: objeto.idunidadmedida,
            }));

            // Quito los items con cantiadd cero para no incluirlos en el payload de persistencia
            movimientos = movimientos.filter((movimiento: { idordencompraitem: number, idarticulo: number, cantidad: number, idunidadmedida: number }) => movimiento.cantidad > 0);


            const fecha = this.form.get('fecha').value;
            const hora = this.form.get('hora').value;

            // Se formatea la fecha nuevamente al formato deseado (YYYY-MM-DD)
            const fecha_formateada = this.datePipe.transform(fecha, 'yyyy-MM-dd');

            // Se concatena a ambas fecha y hora
            const fecha_hora = `${fecha_formateada} ${hora}`;

            const payload: ICreateRemito = {
                remito: {
                    idusuario: this.user.id,
                    fecha_hora: fecha_hora,
                    iddeposito: this.form.get('deposito').value.iddeposito,
                    idordencompra: this.form.get('oc_pendiente').value.idordencompra,
                    observaciones: this.form.get('observaciones').value,
                },
                movimientos: movimientos
            }

            if (this.remito?.idremito) {
                this._remitosService.updateRemito(payload, this.remito.idremito)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            } else {
                this._remitosService.saveRemito(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            }
        }
    }

    /**
    * Este metodo cierra el matdialog
    */
    onCloseMenu() {
        this.dialogRef.close(false)
    }

    // ############### Metodos funcionales ###################

    /**
    * Metodo que ayuda al autocompletado de un input
    * @param deposito 
    * @returns 
    */
    private _filterDepositos(deposito: IDeposito): IDeposito[] {
        const filterValue = deposito.toString().toLowerCase();
        return this.depositos.filter(deposito => deposito.descripcion.toLowerCase().includes(filterValue));
    }

    /**
    * Metodo que ayuda al autocompletado de un input
    * @param proveedor 
    * @returns 
    */
    private _filterProveedores(proveedor: IProveedor): IProveedor[] {
        const filterValue = proveedor.toString().toLowerCase();
        return this.proveedores.filter(proveedor => proveedor.RazonSocial.toLowerCase().includes(filterValue));
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input) {
        this.form.get(input).setValue('');
        if (input == "proveedor") {
            this.form.get('oc_pendiente').setValue('');
            this.getOrdenCompras();
        }

        this.dataSource = new MatTableDataSource([]);
    }

    /**
    * Funcion para contar las palabras en las observaciones
    */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
    * Funcion para verificar si se ha alcanzado el límite de caracteres en las observaciones
    */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }

    /**
     * Este metodo obtiene el valor de texto del input que lo utilice
     * @param option 
     * @returns 
     */
    getTextInput(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        if (option.RazonSocial) {
            return option.RazonSocial;
        }
        if (option.idordencompra || option.codigo) {
            return option ? `N°${option.numero} | ${option.fecha}` : ''
        }

        return option;
    }

    /**
 * Esta funcion maneja las cambios de seleccion que puedan tener articulos, unidades de medida u ordenes de compra.
 * @param $event 
*/
    seleccionItem($event) {
        // Si se ha seleccionado un proveedor explícitamente, actualiza el campo del proveedor
        if ($event?.Codigo) {
            let filtered = this.orden_compras.filter(oc => oc.proveedor === $event.Codigo); // Buscamos las OC que posee ese proveedor.
            this.oc_pendientes.next(filtered)
            // Actualizamos el campo del proveedor sin emitir eventos
            this.form.get('proveedor').setValue($event, { emitEvent: false });
        }

        // Si se ha seleccionado una OC, actualiza el campo de OC pendiente
        if ($event?.idordencompra) {
            let proveedor = this.proveedores.find(p => p.Codigo === $event.proveedor); // Busco el proveedor
            this.form.get('proveedor').setValue(proveedor, { emitEvent: false }); // Lo seteo en el campo proveedor

            let filtered = this.orden_compras.filter(oc => oc.proveedor === proveedor.Codigo);

            this.oc_pendientes.next(filtered)

            // Filtro los items de la orden de compra pendiente...
            this.items_oc_pendientes = this.items_oc_pendientes_aux
                .filter(item => item.idordencompra === $event.idordencompra) //...filtro todos los items que tengan la misma orden de compra que se selecciono en el select...
                .map(item => { //... Al devolverlo...
                    return {
                        ...item,
                        cantidad_pedida: Number(item.cantidad_pedida), //... Convierto en numeros estos campos que son strings...
                        cantidad_pendiente: Number(item.cantidad_pendiente),
                        cantidad_entregada: Number(item.cantidad_entregada),
                    }
                });

            // Creo la grilla editable
            this.crearGrillaForm(this.items_oc_pendientes);

        }
    }

    /**
    * Metodo encargado de crear una grilla dinamica
    * @param Items 
    */
    crearGrillaForm(Items) {
        this.mov_art_list = Items;

        this.mov_art_list.forEach((item, index, items_array) => {

            // Define los FormControls
            const cantidad_a_recibir_control = new FormControl(
                { value: this.remito?.idremito ? item.cantidad_entregada || null : item.cantidad_pendiente || null, disabled: Number(item.cantidad_pendiente) > 0 ? false : true },
                [Validators.required, Validators.min(0)]
            );

            const cantidad_pendiente_control = new FormControl(
                { value: item.cantidad_pendiente || null, disabled: true },
                [Validators.required, Validators.min(0)]
            );

            const cantidad_pedida_control = new FormControl(
                { value: item.cantidad_pedida || null, disabled: true },
                [Validators.required, Validators.min(0)]
            );

            // Asigna los controles al item
            items_array[index].cantidad_a_recibir = cantidad_a_recibir_control;
            items_array[index].cantidad_pendiente = cantidad_pendiente_control;
            items_array[index].cantidad_pedida = cantidad_pedida_control;
        });

        this.dataSource = new MatTableDataSource(this.mov_art_list);
    }

    setupProveedorValueChange() {
        this.form.get('proveedor').valueChanges.pipe(
            debounceTime(750),
            takeUntil(this._unsubscribeAll)
        ).subscribe(value => {
            if (value?.Codigo) {
                // Filtra las órdenes de compra por proveedor
                const filtered = this.orden_compras.filter(oc => oc.proveedor === value.Codigo);
                this.oc_pendientes.next(filtered); // Actualiza las órdenes de compra disponibles
            } else {
                this.oc_pendientes.next(this.orden_compras); // Limpia si no hay proveedor seleccionado
            }
        });
    }

    setupOrdenCompraValueChange() {
        combineLatest([
            this.oc_pendientes.asObservable(),
            this.form.get('oc_pendiente').valueChanges.pipe(debounceTime(750), startWith(''))
        ])
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(([ordenes, value]) => {
                const filterValue = value?.toString().toLowerCase() || '';
                const filtered = ordenes.filter(oc =>
                    oc.numero.toString().toLowerCase().includes(filterValue) ||
                    oc.fecha.toString().toLowerCase().includes(filterValue)
                );
                this.filteredOrdenesDeCompraSubject.next(filtered); // Emite las órdenes filtradas
            });
    }

}