import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { IUsuario } from '@core/user/user.types';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IRemito } from '../../models/remito';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { DatePipe } from '@angular/common';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { RemitosService } from '../../service/remitos.service';
import { firstValueFrom, map, Observable, startWith, Subject, takeUntil } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
  selector: 'app-filter-menu-remitos',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  providers: [
    { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS },
  ],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  usuarios: IUsuario[] = [];
  filteredUsuarioResponsable: Observable<IUsuario[]>;
  provedores: IProveedor[] = [];
  filteredProveedor: Observable<IProveedor[]>;
  filterDeposito: IDeposito[] = [];
  filterFechas: any = [];
  movimientos_internos_list: IRemito[] = []; //Se crea por sí hace falta crear una copia de los datos y manipularlos de una forma más personalizada
  color_primario: string;
  color_secundario: string;
  default_color: string = 'bg-primary';
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  constructor(
    @Inject('MENU_DATA') public data: any, 
    private _menuService: JedsMenuService, 
    private datePipe: DatePipe, 
    private _remitosService: RemitosService,
    private _empresaService: EmpresaService
  ) { }

  ngOnInit(): void {
    this.formFilters = new FormGroup({
      filtroFechaInicio: new FormControl(('')),
      filtroFechaFin: new FormControl(('')),
      usuario_responsable: new FormControl(('')),
      proveedor: new FormControl(('')),
      deposito: new FormControl((-1)),
    });
    // Subscribe to empresa data
    this._empresaService.empresa$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(empresa => {
          this.color_primario = empresa.color_primario;
          this.color_secundario = empresa.color_secundario;
      });

    // Load empresa data
    this._empresaService.getEmpresa();
    this.getDepositos();
    this.getUsuarios();
    this.getProveedores();
    this.formFilters.patchValue(this.data.filter);

  }

  resetFilters() {
    this.formFilters.reset({
      filtroFechaInicio: '',
      filtroFechaFin: '',
      usuario_responsable: '',
      proveedor: '',
      deposito: -1,
    });
  }

  onCloseMenu(data: any = null) {
    if (data) {
      data.filtroFechaInicio = this.datePipe.transform(this.formFilters.get('filtroFechaInicio').value, 'yyyy-MM-dd');
      data.filtroFechaFin = this.datePipe.transform(this.formFilters.get('filtroFechaFin').value, 'yyyy-MM-dd');
    }
    this._menuService.close(data);
  }

  /**
  * Funcion para limpiar los filtros de rango de fechas
  */
  limpiarFechas() {
    this.formFilters.get('filtroFechaInicio').setValue('');
    this.formFilters.get('filtroFechaFin').setValue('');
  }

  /**
   * Esta funcion es para listas los usuarios en el filtro de grilla.
   */
  async getUsuarios() {
    const Usuarios = await firstValueFrom(this._remitosService.getUsuarios());
    this.usuarios = Usuarios.list;
    this.filteredUsuarioResponsable = this.formFilters.controls['usuario_responsable'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterUsuaurio(value))
    )
  }

  /**
    * Esta funcion es para filtrar los proveedores según el input que se haya ingresado en el input de proveedor.
    * @param value 
    * @returns 
    */
  private _filterUsuaurio(value: string) {
    const filterValue = value.toLowerCase();
    return this.usuarios.filter(usuario =>
      usuario.nick.toLowerCase().includes(filterValue)
    );
  }

  /**
   * Esta funcion es para listas los proveedores en el filtro de grilla.
   */
  async getProveedores() {
    const Proveedores = await firstValueFrom(this._remitosService.getProveedores());
    this.provedores = Proveedores.list;
    this.filteredProveedor = this.formFilters.controls['proveedor'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterProveedor(value))
    )
  }

  /**
    * Esta funcion es para filtrar los proveedores según el input que se haya ingresado en el input de proveedor.
    * @param value 
    * @returns 
    */
  private _filterProveedor(value: string) {
    const filterValue = value.toLowerCase();
    return this.provedores.filter(provedor =>
      provedor.RazonSocial.toLowerCase().includes(filterValue)
    );
  }

  /**
   * Esta funcion es para listas los depositos en el filtro de grilla.
   */
  async getDepositos() {
    const stk_list = await firstValueFrom(this._remitosService.getDepositos(true));
    this.filterDeposito = stk_list.list;
  }
}