import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IRemito } from '../models/remito';
import { RemitosService } from '../service/remitos.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
@Component({
    selector: 'app-remitos',
    templateUrl: './remitos.component.html',
    styleUrls: ['./remitos.component.scss']
})
export class RemitosComponent implements OnInit, OnDestroy {
    remitos: IRemito[];
    public dataSource: MatTableDataSource<IRemito> = new MatTableDataSource();
    headers = ['Fecha', 'Hora', 'Responsable', 'Observaciones', 'Deposito', 'Proveedor', 'Orden compra', 'Acciones'];
    column_params = ['fecha', 'hora', 'nick_usuario', 'observaciones', 'descripcion_deposito', 'descripcion_proveedor', 'nro_orden_compra', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['nick_usuario', 'descripcion_deposito', 'descripcion_proveedor'];
    filter: IFilter = {
        proveedor: '',
        deposito: -1,
        filtroFechaInicio: '',
        filtroFechaFin: ''
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.stock.remitos + '?sort=-fecha_hora';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        private _remitoService: RemitosService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [
            {
                icono: "view",
                nombre_boton: "Ver Remito",
                functionName: 'ver',
                iconoAccion: (remito: IRemito) => 'jedstion:ver',
                iconoAccionado: (remito: IRemito) => '',
                iconoDeshabilitado: (remito: IRemito) => '',
            },
            {
                icono: "Editar",
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (remito: IRemito) => 'jedstion:editar_disabled',
                iconoAccionado: (remito: IRemito) => '',
                iconoDeshabilitado: (remito: IRemito) => '',
            },
            {
                icono: "Eliminar",
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (remito: IRemito) => 'jedstion:eliminar',
                iconoAccionado: (remito: IRemito) => '',
                iconoDeshabilitado: (remito: IRemito) => '',
            }
        ];
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
                data: this.tabla.getElementosExistentes()
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.deposito != -1 ? "filter[iddeposito]=" + this.filter.deposito + '&' : '';
                        this.extraParams += this.filter.usuario_responsable != '' ? "filter[nick_usuario]=" + this.filter.usuario_responsable + '&' : '';
                        this.extraParams += this.filter.proveedor != '' ? "filter[descripcion_proveedor]=" + this.filter.proveedor + '&' : '';
                        this.extraParams += this.filter.filtroFechaInicio != null ? "filter[fecha][gte]=" + this.filter.filtroFechaInicio + '&' : '';
                        this.extraParams += this.filter.filtroFechaFin != null ? "filter[fecha][lte]=" + this.filter.filtroFechaFin + '&' : '';

                        this.tabla.filters(this.filtroBusqueda);
                        // this._searchService.getSearchInput().setValue(this._tabsServices.getFiltroBuscadorPedido());

                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'ver':
                this.verRemito(event);
                break;
            case 'editar':
                this.alert.warning('No puedes modificar un remito. Debes eliminar el registro y volverlo a crear.')
                // this.updateRemito(event);
                break;
            case 'eliminar':
                this.deleteRemito(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveRemito() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Remito creado con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion ve la informacion de un item
     * @param event 
     */
    verRemito(event) {

        const modal_config = {
            model: event.params.data,
            title: `Ver Remito ${event.params.data.fecha} - ${event.params.data.hora}`,
            soloLectura: true
        };

        this.dialogo
            .open(SaveComponent, { data: modal_config, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Remito editado con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteRemito(event) {
        this.confirm.open({
            title: "Eliminar Remito: " + event.params.data.fecha,
            message: `¿Desea eliminar el remito realizado el: ${event.params.data.fecha} ${event.params.data.hora}; del depósito: ${event.params.data.descripcion_deposito} ?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._remitoService.deleteRemito(event.params.data.idremito).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Remito eliminado con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Este remito no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe();
        this.dialogo.closeAll();
    }
}